body{
    margin: 0px;
}

.border{
    border: 2px solid blue;
    margin-top: 5px;
}

.wrapper{
    display: flex;
    height: 98vh;
    flex-direction: column;
    border: 2px solid;
    margin-top: 5px;
}

.width-50{
    width: 50%;
    height: 50vh;

}

.btn{
    padding: 10px 10px 20px 20px;
    margin-right: 15px;
    background-color: grey;
    color: cornsilk;
    border: 2px solid;
    margin: 15px;
}

.loginform{
    display: flex;
    flex-direction: column;
    margin-top: 15px;
    align-items: center;
    font-size: 20px;
    background-color: rgb(240, 134, 14);
    font-family: sans-serif;
    color:seashell;

}

.label{
    display: inline-block;
    width: 200px;
}

.input{
    font-size: 18px;
    width: 250px;
}

.m-top-20{
    margin-top: 15px;
}

.m-left-10{
    margin-left: 10px;
}

.text-box{
    width: 250px;
    height: 50px;
}

.error-msg{
    color: red;
    font-size: 14px;
}

.header{
    border: 2px solid red;
    height: 8%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.search-box{
    font-size: 24px;
    width: 300px;
    height: 25px;
}

.body{ 
    border: 2px solid blue;
    height: 91%;
    display: flex;
    flex-direction: row;
}

.sidebar{
    border: 2px solid red;
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.content{
    border: 2px solid green;
    width: 80%;
}

.active-btn{
    background-color: black;
    border: 2px black;
}

